@mixin default_profile_style {
	border-radius: 10px;
	background: $white;
	@include bx_shadow_profile; }

// filter grey
@mixin grey_filter {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%); }


// linear gradient
@mixin bg_gradient {
	background: linear-gradient(168.82deg, #969338 0.77%, #787632 99.56%); }

// preloader after decor el
@mixin preloader_after {
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: darken($bg_body, 5%);
		transform: translateY(-100%);
		animation: 2s nextLineHorizontal infinite; } }

// sentence to line
@mixin sentence_to_line($sentense_count: 1) {
	display: -webkit-box;
	-webkit-box-orient: vertical;
	overflow: hidden;
	-webkit-box-orient: vertical;
	text-overflow: ellipsis;
	white-space: normal;
	-webkit-line-clamp: $sentense_count; }

// default style for simplebar
@mixin default_simplebar_style {
	.simplebar-track.simplebar-vertical {
		width: 6px; }
	.simplebar-scrollbar {
		width: 12px;
		left: -2.5px; }
	.simplebar-track.simplebar-vertical .simplebar-scrollbar:before {
		opacity: 1; } }


// box shadow
@mixin bx_shadow_cart {
	box-shadow: 0px 0px 15px rgba($black, 0.09); }

@mixin bx_shadow_modal {
	box-shadow: 0px -2px 10px rgba($black, 0.11); }

@mixin bx_shadow_input {
	box-shadow: 0px 0px 10px rgba($black, 0.09); }

@mixin bx_shadow_map {
	box-shadow: 0px 0px 20px rgba($black, 0.11); }

@mixin bx_shadow_filters {
	box-shadow: 0px 5px 20px rgba($black, 0.04); }

@mixin bx_shadow_profile {
	box-shadow: 0px 0px 10px rgba($black, 0.07); }

@mixin bx_shadow_profile_status {
	box-shadow: 0px 10px 10px rgba($black, 0.07); }

@mixin bx_shadow_table {
	box-shadow: 0px 0px 15px rgba($black, 0.03); }

@mixin bx_shadow_delivery {
	box-shadow: 0px -4px 10px rgba($black, 0.07); }


@mixin hide_scrollbar {
	scrollbar-width: none;
	-ms-overflow-style: none;
	&::-webkit-scrollbar {
		display: none; } }

@mixin position_sticky {
	position: sticky;
	position: -webkit-sticky;
	top: 0; }



// calc px to em
$context: 16px;
@function em($pixels, $context: 16px) {
	@return #{$pixels/$context}em; }


// general btn
@mixin general-btn {
	display: inline-block;
	padding: 11px 41px;
	post_btn {}
	color: $white;
	transition: all 0.35s linear;
	border-radius: 5px; }

// btn reset
@mixin btn_reset {
	background: none;
	border: none;
	cursor: pointer;
	@include trs; }



// background setting's
@mixin bg-setting {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center; }

// Transition snippet's
@mixin trs($all: all, $time: 0.3s, $effect: linear, $timeout: 0s) {
	transition: $all $time $effect $timeout; }

