@import "smart-grid";

// Main setting's
::selection {
	background-color: $acceptor;
	color: $white; }

* {
	outline-color: $acceptor; }

html, body {
	height: 100%;
	margin: 0;
	padding: 0;
 }	// height: 9000px

body {
	font-size: 16px;
	min-width: 320px;
	position: relative;
	line-height: 1.5;
	font-family: $ms-m;
	background: $white;
	overflow-x: hidden;
	-webkit-font-smoothing: antialiased;
	// font-variant: small-caps
	// font-variant-caps: inherit
	input[type="number"]::-webkit-outer-spin-button,
	input[type="number"]::-webkit-inner-spin-button {
		-webkit-appearance: none;
		margin: 0; }

	input[type="number"] {
		-moz-appearance: textfield; }

	input[type="number"]:hover,
	input[type="number"]:focus {
		-moz-appearance: number-input; }

	input, textarea, select {
		border-radius: 0;
		-webkit-border-radius: 0px;
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;
		font-variant: small-caps; }

	input, textarea {
		// border: #666 1px solid
		&::placeholder {
			// font-variant-caps: petite-caps
			@include ms_m_12;
			color: $grey;
			@include sm-block() {
 } }				// font-size: 14px
		&:focus:required:invalid {
			border-color: $setting_red; }
		&:required:valid {
			border-color: $setting_green; } }

	input[type="search"] {
		-webkit-appearance: none; } }

ul,li,h1,h2,h3,h4,h5,h6,p {
	margin: 0;
	padding: 0; }

ol {
	padding-inline-start: 0; }

li {
	list-style-type: none; }

a {
	text-decoration: none;
	&:hover {
		text-decoration: none; } }

table {
	border-spacing: 0px;
	border-collapse: collapse; }

.img-responsive {
	display: block;
	max-width: 100%;
	height: auto; }

.text-center {
	text-align: center; }

 ::-webkit-scrollbar-button {
  background-image: url('');
  background-repeat: no-repeat;
  width: 6px;
  height: 0px; }

 ::-webkit-scrollbar-track {
  background-color: rgba($grey, 0.35);
  box-shadow: 0px 0px 3px rgba($grey, 0.35) inner; }

 ::-webkit-scrollbar-thumb {
  -webkit-border-radius: 0;
  border-radius: 0;
  background-color: $acceptor;
  box-shadow: 0px 1px 1px $acceptor inset;
  background-image: url('https://yraaa.ru/_pu/24/59610063.png');
  background-position: center;
  background-repeat: no-repeat; }

 ::-webkit-resizer {
  background-image: url('');
  background-repeat: no-repeat;
  width: 6px;
  height: 0px; }

 ::-webkit-scrollbar {
  width: 6px;
  height: 6px; }

.container {
	@include wrapper();
	box-sizing: border-box;
	@include xl-block() {
		padding-left: 10px;
		padding-right: 10px; } }


.container-fluid {
	@include wrapper-full();
	box-sizing: border-box;
	@include xl-block() {
		padding-left: 10px;
		padding-right: 10px; } }


.row {
	@include row-flex();
	&.row--filters {
		@include sm-block() {
			background: $section_grey_bg; } }
	&.row--breadcrumbs {
		display: none;
		@include sm-block() {
			display: flex; } }
	&.row--desk {
		display: flex;
		@include sm-block() {
			display: none; } }
	&.row--mobile {
		display: none;
		@include sm-block() {
			display: flex;
			background: $section_grey_bg; } }
	&.row--inner {
		margin-left: -20px;
		margin-right: -20px;
		@include xl-block() {
			margin-left: -10px;
			margin-right: -10px; } } }


.row--align-baseline {
	display: flex;
	align-items: baseline; }

.row--align-center {
	display: flex;
	align-items: center; }

.row--ai-fs {
	display: flex;
	align-items: flex-start; }


.body-wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100%;
	overflow: hidden;
	&.body-wrapper--bg-grey {
		background: $section_grey_bg; }
	&.body-wrapper--full-height {
		min-height: 100%;
		height: 100%;
		.main {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			height: calc(100% - 95px);
			min-height: calc(100% - 95px); }
		.header {
			height: 75px; }
		.footer {
			height: 10px; } }
	&.body-wrapper--sticky {
		@include from(1210px) {
			overflow: initial; } } }


.main {
	&.main.main--mobile-grey {
		@include sm-block() {
			background: $section_grey_bg; } }
	&.main--pos-r {
		position: relative; } }

.dflt-styles {
	background: $section_grey_bg; }

.dflt-styles--pt-pb-20-30 {
	padding-top: 20px;
	padding-bottom: 30px;
	@include sm-block() {
		padding-bottom: 10px; } }

.dflt-styles--bg-grey {
	background: $section_grey_bg; }

.dflt-styles--bg-white {
	background: $white; }

.dflt-styles--dflt-pd {
	padding-top: 70px;
	padding-bottom: 70px;
	@include sm-block() {
		padding-top: 20px;
		padding-bottom: 20px; } }


.body-modal-active {
	overflow: hidden; }


.hidden {
	display: none; }

.img-center {
	display: block;
	margin: 0 auto; }


.post-img {
	display: block;
	margin: 0 auto;
	max-width: 100%;
	&.post-img--full {
		width: 100%;
		margin-bottom: 30px; } }

.links-page__link {
	display: block;
	width: 100%;
	margin: 5px;
	color: $setting_red; }

.printed-content {
	display: none; }

// input validation style
.input-valid {
	background: url(../img/general/icon/valid.svg) no-repeat;
	background-size: 17px 17px;
	background-position-x: calc(100% - 8.5px) !important;
	background-position-y: center !important;
	border-color: $setting_green!important;
	transition: background!important;
	&:-webkit-autofill {
		background: red url(../img/general/icon/valid.svg) no-repeat !important; } }

.input-invalid {
	background: url(../img/general/icon/invalid.svg) no-repeat;
	background-size: 17px 17px !important;
	background-position-x: calc(100% - 8.5px) !important;
	background-position-y: center !important;
	border-color: $setting_red!important;
	transition: background!important;
	&:-webkit-autofill {
		 background: red url(../img/content/icon/valid.svg) no-repeat !important; } }


// simple bar styles
.simplebar-offset {
	max-width: 100%; }

.simplebar-scrollbar {
	width: 8px; }

.simplebar-scrollbar:before {
	background: $acceptor; }

.simplebar-track.simplebar-vertical {
	background: $lines_grey; }
