  top: 0;
  left: 0;
  right: 0;
  padding-top: 45px; }

.header__left {
  float: left; }

.header__right {
  float: right;
  width: 60%;
  text-align: right;
  position: relative;
  top: 2px; }

.nav {
  font-size: 14px;
  vertical-align: middle;
  position: relative;
  top: 7px;
  margin-right: 20px; }

.explore {
  display: inline-block; }

.nav {
  display: inline-block;

  a {
    display: inline-block;
    text-decoration: none;
    position: relative;
    margin-left: 26px;
    overflow: hidden;
    padding-bottom: 4px;
    color: #b8bbc4;

    &.is-active:before, &:hover:before {
      -ms-transform: translate(0, 0);
      transform: translate(0, 0); }

    &::before {
      position: absolute;
      left: 0;
      width: 100%;
      bottom: 0;
      height: 2px;
      background: #2addbc;
      content: "";
      -ms-transform: translate(-100%, 0);
      transform: translate(-100%, 0);
      transition: transform .2s linear; } } }

.explore {
  height: 35px;
  line-height: 32px;
  font-size: 14px;
  border: 1px solid #abaeb7;
  border-radius: 10px;
  background: 0 0;
  color: #dfe3ec;
  padding: 0 14px;
  vertical-align: middle; }

.logo {
  color: #fff; }

.nav a {
