// primary btn styles
.general-btn {
	@include general-btn;
	position: relative;
	z-index: 1;
	width: 100%;
	padding: 15px;
	padding-left: 80px;
	padding-right: 80px;
	@include ms_sb_14;
	text-transform: uppercase;
	overflow: hidden;
	box-sizing: border-box;
	border-radius: 7px;
	cursor: pointer;
	&.general-btn--primary {
		background: $acceptor;
		color: $white;
		border: 1px solid $acceptor;
		&:hover {
			color: $acceptor;
			background: $white; } }

	&.general-btn--primary-gradient {
		@include bg_gradient;
		color: $white;
		border: 1px solid $acceptor;
		&:hover {
			color: $acceptor;
			background: $white; } }

	&.general-btn--outline {
		background: $white;
		color: $acceptor;
		border: 1px solid $acceptor;
		&:hover {
			color: $white;
			background: $acceptor; } }

	&.general-btn--light-grey {
		background: $section_grey_bg;
		color: $black;
		border: 1px solid $section_grey_bg;
		&:hover {
			color: $white;
			background: $acceptor; } }

	&.general-btn--grey {
		text-align: center;
		background: rgba($grey, .15);
		color: $grey;
		border: 1px solid rgba($grey, .15);
		&:hover {
			color: $white;
			background: $acceptor; } }

	&.general-btn--white {
		background: $white;
		color: $black;
		border: 1px solid $white;
		&:hover {
			color: $acceptor;
			border-color: $section_grey_bg;
			background: $section_grey_bg; } }

	&.general-btn--dark-white {
		background: darken($white, 5%);
		color: $black;
		border: 1px solid darken($white, 5%);
		&:hover {
			color: $acceptor;
			border-color: $section_grey_bg;
			background: $section_grey_bg; } }

	&.general-btn--green {
		background: $setting_green;
		color: $white;
		border: 1px solid $setting_green;
		&:hover {
			color: $acceptor;
			border-color: $section_grey_bg;
			background: $section_grey_bg; } } }



// styles for ie
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	.general-btn {
		&.general-btn--primary-gradient {
			background: $acceptor; } } }
